import { Component, h, Prop, Host } from '@stencil/core';
import { InfoBannerData } from '../../../global/data/common'
import { ProductBannerData } from '../../../global/data/product'

@Component({
  tag: 'ks-content-info-banner',
  styleUrl: 'info-banner.css'
})
export class InfoBanner {
  @Prop() content: InfoBannerData;
  @Prop() productBanner: ProductBannerData;

  render() {
    const theme = this.content?.theme;

    const themeStyle = theme ? `:root {
      --navbar-color: ${theme.navbarColor} !important;
      --navbar-color-hover: ${theme.navbarColorHover} !important;
      --navbar-color-active: ${theme.navbarColorActive} !important;
      --navbar-category-color: ${theme.categoryColor} !important;
      --navbar-category-hover: ${theme.categoryColorHover} !important;
      --navbar-category-active: ${theme.categoryColorActive} !important;
      --navbar-category-backdrop: ${theme.categoryColorBackdrop} !important;
    }` : null;

    const data = this.productBanner ? this.productBanner : this.content;

    const ariaLabel = `Banner promocyjny - ${data.name}`;

    return <Host style={{backgroundColor: data.color}}>
        <a href={data.link} aria-label={ariaLabel}>
          <ks-img3 sync fit="contain"
            width={data.width}
            height={data.height}
            image={data.image}
            webp={data.webp}
            alt={data.name}
          />
        </a>

        {theme ? <style innerHTML={themeStyle}></style> : null}
    </Host>
  };
}